
import createService from './../service/createService'
import mylistService from './../service/mylistService'
import Loading from './components/loading'
import common from './mixins/common'
import Badge from 'vux/src/components/badge/index.vue'


export default {
data() {
return {
sel_list: [
{
name: '新增申请',
path: '/operation',
query: {},
}, {
name: '我的待办',
path: '/mylist',
query: {},
}, {
name: '我的提交',
path: '/mySubmit',
query: {},
}
],
currentUser: {},
showBadge: false,
}
},
mixins: [common],
components: {
Loading, Badge,
},
methods: {
goForward({path, query}) {
this.$router.push({
path, query
})
},

getBaseInfo() {
return createService.getBaseInfoData().then(data => {
this.currentUser = data || {};
}).catch(e => {
this.showToast(e.message);
});
},

getTasksNumber() {
return mylistService.getTasksNumber().then(({tableContent = []}) => {
let [data = {}] = tableContent;
this.showBadge = data.total && data.total > 0;
}).catch(e => {
this.showToast(e.message);
});
}
},
created() {
let promises = [];
this.showLoading = true;
promises.push(this.getBaseInfo());
promises.push(this.getTasksNumber());
Promise.all(promises).then(() => {
this.showLoading = false;
});
}
}



