
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: "TaskConfirm",
props: {
show: {
type: Boolean,
default: false
},

canEmpty: {
type: Boolean,
default: true
}
},
data() {
return {
showConfirm: false,
reason: '',
}
},
watch: {
show(val) {
if (val) {
this.showConfirm = true;
}
},
showConfirm(val) {
if (!val) {
this.$emit('input', val);
}
},
},
directives: {TransferDom},
components: {
Confirm,
Group,
XTextarea,
},
methods: {

cancel() {
this.reason = '';
this.$emit('on-cancel');
},

confirm() {
this.$emit('on-confirm', this.reason);
if (!this.canEmpty && !this.reason) {
return
}
this.reason = '';
this.showConfirm = false;
},
},
}



