
import createService from './../../service/createService'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import CascadePickers from './../components/CascadePickers'
import common from './../mixins/common'

export default {
components: {
Cell,
Group,
XInput,
Datetime,
XTextarea,
PopupPicker,
CascadePickers
},
data() {
return {
listId: '',
meetList: [
{
title: '酒店名称',
key: 'hotelName',
type: 'text',
}, {
title: '总人数',
key: 'headCount',
type: 'number',
}, {
title: '房间数量',
key: 'roomNumber',
type: 'number',
}, {
title: '天数',
key: 'dayCount',
type: 'number',
}, {
title: '房间均价',
key: 'roomAveragePrice',
type: 'number',
}, {
title: '场地费用',
key: 'siteFees',
type: 'number',
}, {
title: '路费合计',
key: 'wayFees',
type: 'number',
}, {
title: '餐饮合计',
key: 'repastFees',
type: 'number',
}
],
formData: {
province: '', // 省
city: '', // 市
hotelName: '', // 酒店名称
headCount: '', // 总人数
roomNumber: '', // 房间数量
dayCount: '', // 天数
roomAveragePrice: '', // 房间均价
siteFees: '', // 会议场地费用
hotelFees: '', // 住宿合计
wayFees: '', // 路费合计
repastFees: '', // 餐饮合计
personScope: '', // 人员范围
agenda: '', // 会议议程
begin: '', // 始于
end: '', // 止于
costBU: '',// 费用所属事业部
costDepartment: '',// 费用所属部门
},
provinceList: [],
provinceSelected: [],
cityList: [],
citySelected: [],
hasDefault: false,
cascadeValue: {},
sessionKey: '',
formKey: '',
transCode: '',
showPage: true,
taskId: '',
baseinfo: {},
}
},
computed: {

totalCost() {
let {roomNumber = 0, roomAveragePrice = 0, siteFees = 0, wayFees = 0, repastFees = 0, dayCount = 0} = this.formData;
return `￥${numberComma(Number(roomNumber) * Number(roomAveragePrice) * Number(dayCount) + Number(siteFees) + Number(wayFees) + Number(repastFees))}`;
},

hotelFees() {
let {roomNumber = 0, roomAveragePrice = 0, dayCount = 0} = this.formData;
return Number(roomNumber) * roomAveragePrice * Number(dayCount);
},
},
mixins: [common],
methods: {
goMylist() { //我的提交
this.$router.push({
path: '/myList',
query: {
listId: this.listId
}
})
},
goflow() {
let lists = [
{
title: '开始日期',
key: 'begin',
}, {
title: '结束日期',
key: 'end',
}, {
title: '省份',
key: 'province',
}, {
title: '城市',
key: 'city',
}, {
title: '酒店名称',
key: 'hotelName',
}, {
title: '总人数',
key: 'headCount',
}, {
title: '房间数量',
key: 'roomNumber',
}, {
title: '天数',
key: 'dayCount',
}, {
title: '房间均价',
key: 'roomAveragePrice',
}, {
title: '场地费用',
key: 'siteFees',
}, {
title: '路费合计',
key: 'wayFees',
}, {
title: '餐饮合计',
key: 'repastFees',
}, {
title: '人员范围',
key: 'personScope',
value: '',
}, {
title: '会议议程',
key: 'agenda',
value: '',
}, {
title: '费用所属事业部',
key: 'costBU',
}, {
title: '费用所属部门',
key: 'costDepartment',
}
];
let jsonData = {
listId: this.listId,
referenceId: this.guid(),
};
let warn = '';
Object.assign(this.formData, this.$refs.cascadePickers.getFormData());
lists.every(item => {
if (!this.formData[item.key]) {
warn = `${item.title}不能为空`;
return false
}
return true
});
if (warn) {
this.showToast(warn);
return
}
let formData = this.formData;

if (this.transCode) {
jsonData.referenceId = this.formData.referenceId;
jsonData.$review = this.formData.review;
jsonData.$review2 = this.formData.review2;
}
jsonData.baseinfo = {
id: this.guid(),
transType2: '会务立项申请',
zhuti: '国富黄金',
effectiveTime: '',
transCode: 'KFSCPCGRK',
statusText: '',
transType: 'tspcchin',
status: '',
comment: '',
fj: [],
...this.baseinfo,
};

jsonData['baseinfoExt#sj'] = {
id: this.guid(),
datetime1: formData.begin,
datetime2: formData.end,
};
jsonData.baseinfoExt = {
id: this.guid(),
varchar1: this.assembleDropDownData(formData.province),
varchar2: this.assembleDropDownData(formData.city),
varchar3: formData.hotelName,
integer1: Number(formData.headCount),
integer2: Number(formData.roomNumber),
integer3: Number(formData.dayCount),
double1: Number(formData.roomAveragePrice),
double3: this.hotelFees, // 住宿合计
double2: Number(formData.siteFees),
double4: Number(formData.wayFees),
double5: Number(formData.repastFees),
double6: Number(this.totalCost.replace(/￥/g, '').replace(/,/g, '')),
varchar4: formData.agenda,
varchar5: formData.personScope
};
jsonData['baseinfoExt#fygs'] = {
id: this.guid(),
varchar6: this.assembleDropDownData(formData.costBU),
varchar7: this.assembleDropDownData(formData.costDepartment),


};
jsonData['$comment'] = {
'baseinfo.comment': jsonData.baseinfo.comment
};
jsonData['baseinfo.fj'] = [];
jsonData.transCode = jsonData.baseinfo.transCode;
sessionStorage.setItem(this.sessionKey, JSON.stringify(jsonData));
this.$router.push({
path: '/flow',
query: {
list: this.listId,
taskId: this.taskId,
}
})
},

assembleDropDownData(value = '') {
return {
text: value,
selection: {
data: {
unitName: value,
id: ''
}
},
value,
}
},

getProvinceList() {
return createService.getAccountingUnitByid().then(data => {
let {tableContent = []} = data;
this.provinceList = tableContent && tableContent.map(item => {
let {unitName} = item;
return {
name: unitName,
value: unitName,
}
});
}).catch(e => {
this.showToast(e.message);
});
},

getCityList() {
this.cityList = [];
if (!this.hasDefault) {
this.citySelected = [];
}
if (!this.formData.province) {
return
}
createService.getAccountingUnitByid({
key: 'N2',
name1: this.formData.province
}).then(data => {
let {tableContent = []} = data;
this.cityList = tableContent && tableContent.map(item => {
let {unitName} = item;
return {
name: unitName,
value: unitName,
}
});
}).catch(e => {
this.showToast(e.message);
});
},

provinceChange(val) {
this.formData.province = val[0] || '';
this.getCityList();
},

cityChange(val) {
this.formData.city = val[0] || '';
},

restoreJsonData(jsonData) {
console.log(jsonData)
let {baseinfo, baseinfoExt} = jsonData;
let sygs = {...jsonData['baseinfoExt#fygs']};
let formData = {
referenceId: jsonData.referenceId,
province: baseinfoExt.varchar1.value, // 省
city: baseinfoExt.varchar2.value, // 市
hotelName: baseinfoExt.varchar3, // 酒店名称
headCount: baseinfoExt.integer1, // 总人数
roomNumber: baseinfoExt.integer2, // 房间数量
dayCount: baseinfoExt.integer3, // 天数
roomAveragePrice: baseinfoExt.double1, // 房间均价
siteFees: baseinfoExt.double2, // 会议场地费用
wayFees: baseinfoExt.double4, // 路费合计
repastFees: baseinfoExt.double5, // 餐饮合计
personScope: baseinfoExt.varchar5, // 人员范围
agenda: baseinfoExt.varchar4, // 会议议程
begin: jsonData['baseinfoExt#sj'].datetime1, // 始于
end: jsonData['baseinfoExt#sj'].datetime2, // 止于
costBU: sygs.varchar6 && sygs.varchar6.value,// 费用所属事业部
costDepartment: sygs.varchar7 && sygs.varchar7.value,// 费用所属部门
review: jsonData.review,
review2: jsonData.review2,
};
this.baseinfo = baseinfo;

this.provinceSelected = [formData.province];
this.citySelected = [formData.city];
this.cascadeValue = {
costBU: formData.costBU,
costDepartment: formData.costDepartment,
};
this.formData = formData;
},

getJsonData() {
return createService.getJsonData(this.transCode).then(({tableContent = []}) => {
let [data = {}] = tableContent;
let jsonData = JSON.parse(data.json_data || '{}');
this.hasDefault = true;
this.showPage = true;
this.restoreJsonData(jsonData);
this.$nextTick(() => {

this.hasDefault = false;
})
}).catch(e => {
this.showToast(e.message);
});
},
},
beforeRouteLeave(to, from, next) {
let {name} = to;
if (name === 'Home') {

sessionStorage.removeItem(this.sessionKey)
}
next();
},
created() {
let {query} = this.$route;
let requestPromises = [];
this.listId = query.list;
this.formKey = query.formKey;
this.transCode = query.transCode;
this.taskId = query.taskId;
this.sessionKey = `${this.listId}-FORMDATA`;
this.showLoading = true;
let formData = sessionStorage.getItem(this.sessionKey);
if (formData) {
formData = JSON.parse(formData);

this.restoreJsonData(formData);
}


if (!formData && this.transCode) {
this.showPage = false;
requestPromises.push(this.getJsonData());
}

this.$nextTick(() => {
requestPromises.push(this.getProvinceList());
requestPromises.push(this.$refs.cascadePickers.init());
Promise.all(requestPromises).then(data => {
this.showLoading = false;
})
})
},
mounted() {
let formData = sessionStorage.getItem(this.sessionKey);
if (formData) {
this.hasDefault = true;
formData = JSON.parse(formData);
this.restoreJsonData(formData);
sessionStorage.removeItem(this.sessionKey);
this.$nextTick(() => {

this.hasDefault = false;
})
}
}
}



