import numberPad from 'vux/src/tools/number/pad.js'

import Loading from './../components/loading'

export default {
data() {
return {
showLoading: false, // 是否展示loading
}
},
components: {Loading},
methods: {

guid() {
let S4 = () => {
return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
};
return (S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4());
},

showToast(test = '') {
this.$vux.toast.show({
text: test,
type: 'text',
position: 'middle',
width: 'auto',
isShowMask: true,
time: 1000
});
},

changeDate(d, hasSecond = false) {
if (!d) {
return '';
}
if (typeof d === 'string') {
d = d.replace(/-/g, '/');
}
let date = new Date(d);
let dateObj = {
Y: date.getFullYear(),
m: numberPad(date.getMonth() + 1),
d: numberPad(date.getDate()),
H: numberPad(date.getHours()),
i: numberPad(date.getMinutes()),
s: numberPad(date.getSeconds()),
};
let fmt = 'Y-m-d';
if (hasSecond) {
fmt = 'Y-m-d H:i:s';
}
Object.entries(dateObj).forEach(([key, value]) => {
fmt = fmt.replace(key, value);
});
return fmt;
},
}
}



