
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import mylistService from '../service/mylistService.js';
import createService from './../service/createService.js';
import RScroll from './components/RScroll'

export default {
name: 'mySubmit',
components: {
Tab,
TabItem,
RScroll,
},
data() {
return {
avatar: '',
listData: [], // 数据列表
page: 1, // 当前页数
limit: 10, // 每页条数
hasNext: true, // 是否有下一页
tabList: [
{
name: '市场费用',
value: '市场宣传费用预申请'
}, {
name: '会务立项',
value: '会务立项申请'
}, {
name: '房屋立项',
value: '房屋立项申请'
}, {
name: '固定资产',
value: '固定资产预申请'
}
], // tab列表
activeTab: '市场宣传费用预申请', // 选中的tab
userInfo: {}, // 用户信息
scrollOptions: { // 滚动配置
click: true,
pullUpLoad: true,
}
};
},
watch: {
$route: {
handler(to, from) {
if (to.name === 'Home') {
this.activeTab = this.tabList[0].value;
this.resetCondition();
from.meta.reload = true;
}
}
}
},
filters: {
dateFormat,
},
methods: {

getUser() {
return createService.getBaseInfoData().then((data = {}) => {
this.userInfo = data;
}).catch(e => {
this.showToast(e.message);
});
},

selStatus(val) {
this.activeTab = this.tabList[val].value;
this.resetCondition();
this.loadData();
},

goDetail(item) {
let {listID, transCode, taskId, starterId} = item;
let isMyTask = '1'; 
let canSubmit = '0'; 
let map = {
SCEX: '/spreadDetail',
MEEX: '/meetingDetail',
HOEX: '/houseDetail',
GDZC: '/assetsDetail',
};
let key = transCode.split('_')[0];
this.$router.push({
path: map[key],
query: {
list: listID,
transCode,
taskId,
canSubmit,
isMyTask,
}
});
},

loadData() {
let filter = [{
operator: 'like',
value: this.activeTab,
property: 'title'
}];
return mylistService.getListStartedByMe({
page: this.page,
start: (this.page - 1) * this.limit,
limit: this.limit,
filter: JSON.stringify(filter),
}).then(({dataCount = 0, tableContent = []}) => {
this.hasNext = dataCount > (this.page - 1) * this.limit + tableContent.length;
this.listData = this.page === 1 ? tableContent : [...this.listData, ...tableContent];
this.$nextTick(() => {
this.$refs.bScroll.finishPullUp();
})
}).catch(e => {
this.showToast(e.message);
})
},

resetCondition() {
this.listData = [];
this.page = 1;
this.hasNext = true;
this.$refs.bScroll && this.$refs.bScroll.scrollTo(0, 0);
},

onPullingUp() {
this.page++;
this.loadData();
},

showToast(test = '') {
this.$vux.toast.show({
text: test,
type: 'text',
position: 'middle',
width: 'auto',
isShowMask: true,
time: 1000
});
}
},
created() {
let {avatar = ''} = JSON.parse(sessionStorage.getItem('ROSE_LOGIN_TOKEN') || '{}');

this.avatar = avatar;
this.getUser();
this.loadData();
},
activated() {
let reload = this.$route.meta.reload;
if (reload) {
this.loadData();
this.$route.meta.reload = false;
}
}
};



