
import Flow from 'vux/src/components/flow/flow.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
props: {
workFlow: {
type: Array,
default() {
return {}
}
}
},
name: "FlowDetail",
data() {
return {
commentList: [],
flowList: [
{
stateDone: true,
lineDone: false,
tip: '进行中',
title: '省长'
}, {
stateDone: false,
lineDone: false,
tip: '',
title: '部门'
}, {
stateDone: false,
lineDone: false,
tip: '',
title: '副总'
}, {
stateDone: false,
lineDone: false,
tip: '',
title: '财务'
}, {
stateDone: false,
lineDone: false,
tip: '',
title: '王珏 - 总裁'
},
],
}
},
watch: {
workFlow: {
handler() {
this.assembleWorkFlow();
}
}
},
components: {
Flow,
Cell,
Group,
FlowLine,
FlowState,
LoadMore,
},
methods: {

assembleWorkFlow() {
let showList = ['常委', '部门主管', '副总裁', '分管副总', '财务', '总裁'];
let workFlow = [...this.workFlow];
let [firstNode = {}] = workFlow;
let secondNode = {};
let lastNode = workFlow[workFlow.length - 1] || {};
workFlow && workFlow.forEach((item, index) => {
item.userName = item.userName && item.userName.split('-')[0];

if (item.endTime && showList.indexOf(item.nodeName) !== -1) {
this.commentList.push({
title: item.userName,
desc: item.message || '无',
time: item.startTime,
})
}
if (item.nodeName === '常委' || item.nodeName === '部门主管') {
this.flowList[1].title = item.userName;
}
if (item.nodeName === '副总裁' || item.nodeName === '分管副总') {
this.flowList[2].title = item.userName;
}
if (item.nodeName === '财务') {
this.flowList[3].title = item.userName;
}
});
this.flowList[0].title = firstNode.userName;
this.assembleFlow(lastNode);
},

assembleFlow(item) {
let tmp = [];

let done = {
stateDone: true,
lineDone: true,
tip: '',
};

let doing = {
stateDone: true,
lineDone: false,
tip: '进行中',
};
switch (item.nodeName) {
case 'Start':
this.governor = doing;
break;
case '部门主管':
case '常委':
this.flowList[0] = {...this.flowList[0], ...done};
this.flowList[1] = {...this.flowList[1], ...doing};
break;
case '分管副总':
case '副总裁':
this.flowList[0] = {...this.flowList[0], ...done};
this.flowList[1] = {...this.flowList[1], ...done};
this.flowList[2] = {...this.flowList[2], ...doing};
break;
case '财务':
this.flowList[0] = {...this.flowList[0], ...done};
this.flowList[1] = {...this.flowList[1], ...done};
this.flowList[2] = {...this.flowList[2], ...done};
this.flowList[3] = {...this.flowList[3], ...doing};
break;
case '生效表单':
break;
case '总裁':
case 'End':
this.flowList.forEach(item => {
tmp.push({
...item,
...done,
});
});
this.flowList = tmp;
break;
default:
break;
}
},
showToast(text = '') {
this.$vux.toast.show({
text: text,
type: 'text',
position: 'middle',
width: 'auto',
isShowMask: true,
});
},
},
created() {
this.assembleWorkFlow();
}
}



