import axios from 'axios';
import $axios from '../plugins/ajax'
import conf from "../plugins/ajax/conf";
import AlertModule from 'vux/src/plugins/alert/module'



let errorHandler = (e) => {
AlertModule.show({
content: e.message,
});
return Promise.reject(e)
};

let spread={


getFormView(data){
return $axios.ajax({
url:'/H_roleplay-si/ds/list/getFormViewById',
data:data,
})
},


getAccounting(data){
return $axios.ajax({
url:'/H_roleplay-si/ds/getAccountingUnitByid',
data:data,
}).catch(e => {
return errorHandler(e);
});
},


getBaseInfo(){
return $axios.ajax({
url:'/H_roleplay-si/trans/getModelData?refresh=true&dsCode=getUserDetails',
})
},

getMarketInfo(key,name){
return $axios.ajax({
url:'/H_roleplay-si/ds/getMarketInformation',
data:{
key: key,
name1: name,
page: 1,
start: 0,
limit: 10000
}
}).catch(e => {
return errorHandler(e);
});
}
};

export default spread;


